import React from 'react';
import { NavLink } from 'react-router-dom';


const All = (props) => {
    return (
        <>
            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid nav_bg">
                    <div className="row">
                        <div className="col-10 mx-auto m-5">
                            <div className="row">
                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                                    <h1>
                                        {props.heading} <strong className="brand-name">TechGuySaksham</strong>
                                    </h1>
                                    <h2 className="my-3">
                                        {props.subheading}
                                    </h2>
                                    <div className="mt-3">
                                        <NavLink to={props.visit} className=" p-1 btn-get-started">{props.btn}</NavLink>
                                    </div>
                                </div>
                            <div className="col-lg-6 order-1 order-lg-2 header-img">
                                <img src={props.imgsrc} alt="hehe" className="img-fluid animated" />
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default All;
